/* original: static/css/custom.css + templates/base.html inline styles */
/* CampusNexus Global Styles */

:root {
  --white: #ffffff;
  --light-blue-grey: #676F9D;
  --medium-blue-grey: #424760;
  --dark-blue-grey: #203250;
  --orange: #f9b17a;
  --text-black: #212529;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  max-width: 100vw;
  overflow-x: hidden;
}

body {
  background: var(--dark-blue-grey);
  color: var(--white);
  font-family: 'Raleway', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.navbar {
  background: var(--dark-blue-grey);
  border-bottom: 2px solid var(--orange);
  box-shadow: 0 2px 10px rgba(249, 177, 122, 0.3);
}

.navbar-brand {
  color: #ffffff !important;
  font-weight: bold;
  font-size: 1.5rem;
}

.navbar-nav .nav-link {
  color: #ffffff !important;
  transition: color 0.3s;
}

.navbar-nav .nav-link:hover {
  color: var(--orange) !important;
}

.card {
  background: var(--medium-blue-grey);
  border: 1px solid var(--light-blue-grey);
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
  color: var(--white);
  margin-bottom: 20px;
}

.card-body {
  color: var(--white) !important;
}

.card-body p, .card-body li, .card-body span, .card-body div {
  color: var(--white) !important;
  font-weight: 500;
}

.card-body strong {
  color: var(--white) !important;
  font-weight: 600;
}

.card-header {
  background: var(--dark-blue-grey);
  color: var(--white);
  border-bottom: 2px solid var(--orange);
  border-radius: 15px 15px 0 0 !important;
  font-weight: 600;
}

.btn-primary {
  background: var(--orange);
  border: none;
  color: var(--white);
  transition: transform 0.2s, box-shadow 0.2s;
  font-weight: 600;
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(249, 177, 122, 0.4);
  background: #f8a569;
  color: var(--white);
}

.btn-secondary {
  background: var(--light-blue-grey);
  border: none;
  color: var(--white);
  transition: transform 0.2s;
  font-weight: 600;
}

.btn-secondary:hover {
  transform: translateY(-2px);
  background: var(--medium-blue-grey);
  color: var(--white);
}

.btn-success, .btn-warning, .btn-danger {
  border: none;
  font-weight: 600;
  transition: transform 0.2s;
}

.btn-success:hover, .btn-warning:hover, .btn-danger:hover {
  transform: translateY(-2px);
}

.form-control, .form-select {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid var(--light-blue-grey);
  color: var(--white);
  border-radius: 8px;
}

.form-control:focus, .form-select:focus {
  background: rgba(255, 255, 255, 0.15);
  border-color: var(--orange);
  color: var(--white);
  box-shadow: 0 0 0 0.25rem rgba(249, 177, 122, 0.25);
}

.form-control::placeholder {
  color: rgba(255, 255, 255, 0.6);
}

.form-label {
  color: var(--white);
  font-weight: 500;
}

.alert {
  border-radius: 10px;
  border: none;
}

.list-group-item {
  background: var(--medium-blue-grey);
  border: 1px solid var(--light-blue-grey);
  color: var(--white);
}

.list-group-item a {
  color: var(--orange);
  text-decoration: none;
}

.list-group-item a:hover {
  color: #f8a569;
}

.badge {
  font-weight: 600;
  padding: 0.5em 0.75em;
}

a {
  color: var(--orange);
  text-decoration: none;
}

a:hover {
  color: #f8a569;
}

.event-card {
  transition: all 0.3s ease;
  cursor: pointer;
}

.event-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px rgba(249, 177, 122, 0.4);
}

.star-rating input[type="radio"] {
  display: none;
}

.star-rating label {
  font-size: 2rem;
  color: #676F9D;
  cursor: pointer;
  transition: color 0.2s;
}

.star-rating input[type="radio"]:checked ~ label,
.star-rating label:hover,
.star-rating label:hover ~ label {
  color: #f9b17a;
}

.fc {
  background: rgba(32, 50, 80, 0.8);
  color: #ffffff;
}

.fc-toolbar-title {
  color: #ffffff !important;
}

.fc-button-primary {
  background: #f9b17a !important;
  border: none !important;
}

.fc-event {
  background: #f9b17a !important;
  border-color: #676F9D !important;
}

.hot-badge {
  background: linear-gradient(135deg, #ff6b6b, #ff8e53);
  color: white;
  padding: 0.25rem 0.75rem;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 600;
  display: inline-block;
}

.container {
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.table {
  color: var(--white);
}

.table thead {
  background: var(--dark-blue-grey);
  border-bottom: 2px solid var(--orange);
}

.table tbody tr {
  background: var(--medium-blue-grey);
  border-bottom: 1px solid var(--light-blue-grey);
}

.table tbody tr:hover {
  background: var(--light-blue-grey);
}
